::selection {
  background: #1a75ff;
  color: #fff;
}

.wrapper {
  display: none;
  overflow: hidden;

  position: fixed;
  /* Stay in place */
  z-index: 1;
  /* Sit on top */
  left: 0;
  top: 0;

  max-width: 450px;
  background: rgba(32, 32, 36, 1);
  padding: 30px;
  margin: 20px;
  border-radius: 18px;
  border: 2px solid #111111;
  box-shadow: 0px 15px 20px rgba(0, 0, 0, 0.1);
}

.wrapper .title-text {
  display: flex;
  width: 200%;
}

.wrapper .slide-controls {
  position: relative;
  display: flex;
  height: 50px;

  overflow: hidden;
  margin: 30px 0 10px 0;
  justify-content: space-between;
  border: 2px solid #f6f6f6;
  border-radius: 40px;
}

.slide-controls .slide {
  height: 100%;
  width: 100%;
  color: #fff;
  font-size: 16px;
  font-weight: 500;
  text-align: center;
  line-height: 48px;
  cursor: pointer;
  z-index: 1;
  transition: all 0.6s ease;
}

.slide-controls label.signup {
  color: white;
}

.slide-controls .slider-tab {
  position: absolute;
  height: 100%;
  width: 50%;
  left: 0;
  z-index: 0;
  border-radius: 40px;
  background: rgba(79, 46, 232, 1);
  transition: all 0.6s cubic-bezier(0.68, -0.55, 0.265, 1.55);
}

input[type='radio'] {
  display: none;
}

#signup:checked ~ .slider-tab {
  left: 50%;
}

#signup:checked ~ label.signup {
  color: #fff;
  cursor: default;
  user-select: none;
}

#signup:checked ~ label.login {
  color: white;
}

#login:checked ~ label.signup {
  color: white;
}

#login:checked ~ label.login {
  cursor: default;
  user-select: none;
}

.wrapper .form-container {
  width: 100%;
  overflow: hidden;
}

.form-container .form-inner {
  display: flex;
  width: 200%;
}

.form-container .form-inner form {
  width: 50%;
  transition: all 0.6s cubic-bezier(0.68, -0.55, 0.265, 1.55);
}

.form-inner form .field {
  height: 50px;

  margin-top: 20px;
}

.form-inner form .field input {
  height: 100%;
  width: 94%;
  outline: none;
  color: #f6f6f6;
  padding-left: 15px;
  border-radius: 40px;
  border: 2px solid #f6f6f6;
  border-bottom-width: 2px;
  font-size: 16px;
  transition: all 0.3s ease;
  background: rgba(32, 32, 36, 1);
}

.form-inner form .field input:focus {
  border-color: #f6f6f6;
  /* box-shadow: inset 0 0 3px #fb6aae; */
}

.form-inner form .field input::placeholder {
  color: #f6f6f6;
  transition: all 0.3s ease;
}

form .field input:focus::placeholder {
  color: #f6f6f6;
}

.form-inner form .pass-link {
  margin-top: 25px;
}

.form-inner form .signup-link {
  text-align: center;
  margin-top: 30px;
}

.form-inner form .pass-link a,
.form-inner form .signup-link a {
  color: white;
  text-decoration: none;
}

.form-inner form .pass-link a:hover,
.form-inner form .signup-link a:hover {
  text-decoration: underline;
}

form .btn {
  height: 50px;
  width: 100%;
  border-radius: 40px;
  position: relative;
  overflow: hidden;
}

form .btn .btn-layer {
  height: 100%;
  width: 300%;
  position: absolute;
  left: -100%;
  background: #f6f6f6;
  transition: all 0.4s ease;
}

form .btn:hover .btn-layer {
  left: 0;
}

.btn-queue-modal [type='submit'] {
  height: 100%;
  width: 100%;
  z-index: 1;
  position: relative;
  background: none;
  border: none;
  color: #202024;
  padding-left: 0;
  border-radius: 15px;
  font-size: 20px;
  font-weight: 500;
  cursor: pointer;
}

@media screen and (min-width: 768px) {
  .wrapper {
    border: 2px solid #f6f6f6;
  }
}
