.wrapper {
  z-index: 1;
  max-width: 450px;
  background: #202024;
  border: 2px solid #111;
  border-radius: 18px;
  margin: 20px;
  padding: 30px;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  overflow: hidden;
  box-shadow: 0 15px 20px rgba(0, 0, 0, .1);
}

.wrapper .title-text {
  width: 200%;
  display: flex;
}

.wrapper .slide-controls {
  height: 50px;
  border: 2px solid #f6f6f6;
  border-radius: 40px;
  justify-content: space-between;
  margin: 30px 0 10px;
  display: flex;
  position: relative;
  overflow: hidden;
}

.slide-controls .slide {
  height: 100%;
  width: 100%;
  color: #fff;
  text-align: center;
  cursor: pointer;
  z-index: 1;
  font-size: 16px;
  font-weight: 500;
  line-height: 48px;
  transition: all .6s;
}

.slide-controls label.signup {
  color: #fff;
}

.slide-controls .slider-tab {
  height: 100%;
  width: 50%;
  z-index: 0;
  background: #4f2ee8;
  border-radius: 40px;
  transition: all .6s cubic-bezier(.68, -.55, .265, 1.55);
  position: absolute;
  left: 0;
}

input[type="radio"] {
  display: none;
}

#signup:checked ~ .slider-tab {
  left: 50%;
}

#signup:checked ~ label.signup {
  color: #fff;
  cursor: default;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

#signup:checked ~ label.login, #login:checked ~ label.signup {
  color: #fff;
}

#login:checked ~ label.login {
  cursor: default;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.wrapper .form-container {
  width: 100%;
  overflow: hidden;
}

.form-container .form-inner {
  width: 200%;
  display: flex;
}

.form-container .form-inner form {
  width: 50%;
  transition: all .6s cubic-bezier(.68, -.55, .265, 1.55);
}

.form-inner form .field {
  height: 50px;
  margin-top: 20px;
}

.form-inner form .field input {
  height: 100%;
  width: 94%;
  color: #f6f6f6;
  background: #202024;
  border: 2px solid #f6f6f6;
  border-radius: 40px;
  outline: none;
  padding-left: 15px;
  font-size: 16px;
  transition: all .3s;
}

.form-inner form .field input:focus {
  border-color: #f6f6f6;
}

.form-inner form .field input::-ms-input-placeholder {
  color: #f6f6f6;
  transition: all .3s;
}

.form-inner form .field input::placeholder {
  color: #f6f6f6;
  transition: all .3s;
}

form .field input:focus::-ms-input-placeholder {
  color: #f6f6f6;
}

form .field input:focus::placeholder {
  color: #f6f6f6;
}

.form-inner form .pass-link {
  margin-top: 25px;
}

.form-inner form .signup-link {
  text-align: center;
  margin-top: 30px;
}

.form-inner form .pass-link a, .form-inner form .signup-link a {
  color: #fff;
  text-decoration: none;
}

.form-inner form .pass-link a:hover, .form-inner form .signup-link a:hover {
  text-decoration: underline;
}

form .btn {
  height: 50px;
  width: 100%;
  border-radius: 40px;
  position: relative;
  overflow: hidden;
}

form .btn .btn-layer {
  height: 100%;
  width: 300%;
  background: #f6f6f6;
  transition: all .4s;
  position: absolute;
  left: -100%;
}

form .btn:hover .btn-layer {
  left: 0;
}

.btn-queue-modal [type="submit"] {
  height: 100%;
  width: 100%;
  z-index: 1;
  color: #202024;
  cursor: pointer;
  background: none;
  border: none;
  border-radius: 15px;
  padding-left: 0;
  font-size: 20px;
  font-weight: 500;
  position: relative;
}

